export const authtenticateGQLTAG = `mutation authenticate(
    $token: String!
    $roleType: String!
    $authenticateType: String!
    $extra: JSON
  ) {
    authenticate(
      token: $token
      roleType: $roleType
      authenticateType: $authenticateType
      extra: $extra
    ) {
      data
    }
  }
  
  `;

