export const createCuisineTypeGQLTAG = `mutation createCuisineTypeMaster($cusineTypeName: String!, $chefId: String!) {
 createCuisineTypeMaster(
   input: {
     cuisineTypeMaster: {
       cusineTypeName: $cusineTypeName
       cuisineTypeDesc: $cusineTypeName
       isManuallyYn: true
       chefId: $chefId
     }
   }
 ) {
   cuisineTypeMaster {
     cuisineTypeId
     cusineTypeName
     cuisineTypeDesc
     isManuallyYn
     chefId
     isAdminApprovedYn
     createdAt
     updatedAt
   }
 }
}`;

